@font-face {
    font-family: 'Enriqueta';
    src: local('Enriqueta Regular'), local('Enriqueta-Regular'), url('enriquetaregular.woff2') format('woff2'), url('enriquetaregular.woff') format('woff'), url('enriquetaregular.ttf') format('truetype');
    font-weight: 400;
    font-style: normal;
}
@font-face {
    font-family: 'Enriqueta';
    src: local('Enriqueta Bold'), local('Enriqueta-Bold'), url('enriquetabold.woff2') format('woff2'), url('enriquetabold.woff') format('woff'), url('enriquetabold.ttf') format('truetype');
    font-weight: 700;
    font-style: normal;
}
